/* Authpopup.css */

.popup {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999; /* ensure it's above everything */
  overflow: auto; /* allow scrolling if content overflows */
}

.authform {
  background: blak;
  padding: 32px;
  border-radius: 16px;
  max-width: 90%;
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.left {
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
}

.right {
  width: 100%;
}

.right h1 {
  color: dimgray;
  text-align: center;
}
